@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:300,400,500,700|Zilla+Slab:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-bar {
  margin-left: 0;
  border-bottom: 0.1rem solid goldenrod;
}

.logout {
  color: black;
}

/* .headRow {
    padding-top: 150%;
    
} */

.homeMain {
   display: flex;
   justify-content: space-between;

}
.sideNote {
   border-left: 1px solid goldenrod;
   border-right: 1px solid goldenrod;
   width: 35vw;
   font-family: 'Zilla Slab', serif;
  color: whitesmoke;
  font-size: 1.2rem;
}

p {
   color: white;
}

.topHead {
  padding-top: 4%;
}
.ofTheDay {
   margin-top: 4%;
   border-top: 4px solid rgba(218, 165, 32, 0.4);
   border-left: 4px solid rgba(218, 165, 32, 0.4);
   border-radius: 16px;
   background-color: #daa520;
   background-clip: padding-box;
   width: 50vw;
}

.ofTheDayHead {
   margin: 2% 2% 0 2%;
   border-bottom: 4px solid #daa520;
   color: rgb(31, 30, 30);
}
.randomSyn {
   margin: 0 2% 2% 2%;
   padding-bottom: 4%;
   color: black;
   width: -webkit-fit-content;
   width: -moz-fit-content;
   width: fit-content;
   height: -webkit-fit-content;
   height: -moz-fit-content;
   height: fit-content;
}
.randomSyn span{
  color: rgb(31, 30, 30);
}
.randomLink:hover {
  color: black;
}


article {
   margin: 0px 56px;
}

.asideAbout {
   text-align: center;
   padding-top: 4%;
}
.viewAll {
   text-decoration: underline;
}

.viewAll:hover {
   text-decoration: underline;
}
.homeList li {
   list-style: none;
   text-transform: uppercase;
}

.homeList h3 {
   color: goldenrod;
}
.homeList {
   display: grid;
   grid-template-columns: 1fr 1fr 1fr;
   grid-template-rows: repeat(2, -webkit-max-content);
   grid-template-rows: repeat(2, max-content);
   margin-top: 4%;
   padding-right: 8%;
}

.movieInfo {
  padding-right: 44%;
}
/* .notification p {
  color: rgb(155, 37, 37);
} */
.movieInfo p {
  margin-top: 2%;
  color: #f5f5f5;
  font-weight: bold;
  font-family: 'Zilla Slab', serif;
}
.movieInfo h2 {
  color: #d9a520;
  text-decoration: none;
}
.movieInfo h6 {
  color: #a9a5a5;
  text-transform: uppercase;
  /* font-family: 'Zilla Slab', serif; */
}

.submitRating, .editRating{
  display: inline;
  text-transform: uppercase;
  font-family: 'Zilla Slab', serif;
}

.editRating{
  font-weight: 100;
}
.userInfo h2 {
  color: goldenrod;
}
.userInfo li {
  text-transform: uppercase;
}

.title-submit {
  width: 28%;
  padding-top: 4%;
}

.form-group .genre-submit {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.synopsis-submit {
  width: 56%;
}

.form-group button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: whitesmoke;
  font-weight: 800;
  margin: 1% 0;
  /* font-family: Arial, Helvetica, sans-serif; */
  text-transform: uppercase;
}

.submitForm {
  color: goldenrod;
  font-family: 'Zilla Slab', serif;
  font-weight: bold;
}

.allMovies li {
  list-style: none;
  text-transform: uppercase;
}
.viewAll {
  text-transform: uppercase;
}

.allMovies h2 {
  color: goldenrod;
}

body {
  margin: 0;
  padding: 0;
   background-image: linear-gradient(-0.9turn, #222121, 95%, white);
   background-repeat: no-repeat;
   background-color: black;
   height: 125vh;
}
a {
   text-decoration: none;
   color: whitesmoke;
}
a:hover {
   text-decoration: none;
   color: goldenrod;
}
h1,
h2,
h3,
h4 {
   font-family: 'Zilla Slab', serif;
   color: whitesmoke;
}

span {
   font-family: 'Zilla Slab', serif;
   color: whitesmoke;
}
.appMain {
   padding-left: 8%;
   height: 90vh;
}

.loginButtons {
   font-family: 'Zilla Slab', serif;
   display: flex;
}

.headerButton p {
   padding-left: 1%;
   display: block;
}
.headerButton {
   display: flex;
   justify-content: space-evenly;
   margin-bottom: 2%;
}

.login-form {
   padding-top: 8%;
   margin: auto;
   width: 24%;
}

@media (max-width: 600px) {
   .synopsis-submit {
      width: 100%;
   }
   .movieInfo {
      padding-right: 4%;
   }

   .ofTheDay {
      width: 90vw;
   }

   .homeMain {
      height: 100vh;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
   }

   .sideNote {
      width: 90vw;
      border: none;
   }
   .homeList {
      display: grid;
      grid-template-columns: 1fr 1fr;
   }

   .container {
     flex-direction: column
   }
  .navbar {
    flex-direction: column
  }
    .login-form {
      width: 80vw;
    }  

  .body {
    height: 100%;
    background-position: center;
    background-size: cover;
  }
   /* nav-bar should become a dropdown */
}

